.container{
	max-width:120rem;
	margin:8rem auto;
	background-color: var(--color-grey-light-1);
	box-shadow:var(--shadow-dark);
	min-height:50rem;
}


.header{
	height: 7rem;
	font-size: 1.4rem;
	border-bottom:var(--line);
	display: flex;
	justify-content: space-between;
	align-items: center;

}

.content{
	display: flex;
}

.sidebar{
	background-color: var(--color-grey-dark-1);
	flex:0 0 18%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.detail{
	font-size: 1.2rem;
	display: flex;
	padding: 4.5rem;
	background-color: var(--color-grey-light-1);
	border-bottom: var(--line);
}

.description{
	background-color: #fff;
	box-shadow: var(--shadow-light);
	padding: 3rem;
	flex:0 0 60%;
	margin-right: 4.5rem;
}

.user-reviews{
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
}