.logo{
	height: 8rem;
	margin-left: 2rem;
}

.search{
	flex: 0 0 40%;
	display: flex;
	align-items: center;
	justify-content: center;

	&__input{
		font-family:inherit;
		font-size: inherit;
		color: inherit;
		background-color: var(--color-grey-light-2);
		border:none;
		padding: .7rem 2rem;
		border-radius: 100px;
		width:90%;
		transition: all .2s;
		margin-right: -3.25rem;

		&:focus{
			outline:none;
			width: 100%;
		}

		&::input-placeholder{
			font-weight: 100;
			color:var(--color-grey-light-2);
		}
	}

	&__input:focus + &__button{
		background-color: var(--color-grey-light-3);
	}

	&__button{
		border:none;
		background-color: var(--color-grey-light-2);

		&:focus{
			outline:none;
		}

		&:active{
			transform: translateY(2px);
		}
	}

	&__icon{
		height: 2rem;
		width: 2rem;
		filter: invert(69%) sepia(5%) saturate(9%) hue-rotate(32deg) brightness(87%) contrast(96%);
	}
}

.user-nav{
	align-self: stretch;
	display: flex;
	align-items: center;

	& > *{
		padding: 0 2rem;
		cursor: pointer;
		height: 100%;
		display: flex;
		align-items: center;
	}

	& > *:hover{
		background-color: var(--color-grey-light-2);
	}


	&__icon-box{
	position: relative;
}

	&__icon{
		height: 2.25rem;
		width: 2.25rem;
		filter: invert(43%) sepia(88%) saturate(14%) hue-rotate(314deg) brightness(89%) contrast(83%);
	}

	&__notification{
		font-size: .8rem;
		height: 1.75rem;
		width: 1.75rem;
		border-radius: 50%;
		background-color: var(--color-primary);
		color:#fff;
		position: absolute;
		top: 1.5rem;
		right: 1.1rem;
		display: flex;
		justify-content: center;
		align-items:center;
	}

	&__photo{
		height:3.75rem;
		border-radius: 50%;
		margin-right: 1rem;
	}
}

.side-nav{
	font-size: 1.4rem;
	list-style:none;
	margin-top: 3.5rem;

	&__item{
		position: relative;

		&:not(:last-child){
			margin-bottom: .5rem;
		}

	}

	&__item::before{
		content: "";
		position: absolute;
		top:0;
		left: 0;
		height: 100%;
		width: 3px;
		background-color: var(--color-primary);
		transform: scaleY(0);
		transition: transform .2s,
					width .4s cubic-bezier(1,0,0,1) .2s,
					background-color.1s;


	}

	&__item:hover::before,
	&__item--active::before{
		transform: scaleY(1);
		width: 100%;
	}

	&__item:active::before{
		background-color: var(--color-primary-light);
	}

	&__icon{
		width: 0.5rem;
		height: 0.5rem;
		&--1,&--2,&--3{
		filter: invert(99%) sepia(0%) saturate(0%) hue-rotate(129deg) brightness(103%) contrast(100%);
		}
		&--4{
			z-index: 100;
		}
	}

	&__link{
		color: var(--color-grey-light-1);
		text-transform: uppercase;
		display: block;
		padding: 1.3rem 2.5rem;
		display: flex;
		align-items: center;
		z-index: 100;
		span{
			margin-left: 2rem;
			z-index: 100;
		}
	}
}

.legal{
	color: var(--color-grey-light-1);
	text-align: center;
	padding: 2.5rem;
}


.gallery{
	display: flex;

	&__photo{
		width: 100%;
		height: 110%;

	}
}

.overview{
	margin-top: 2rem;
	display: flex;
	align-items: center;
	border-bottom: var(--line);

	&__heading{
		font-size: 2.25rem;
		font-weight: 300;
		text-transform: uppercase;
		letter-spacing: 1px;
		padding: 1.5rem 3rem;
	}

/*Important*/
	&__stars{
		margin-right: auto;
		display: flex;
	}

	&__icon-star, &__icon-location{
		width: 1.75rem;
		height: 1.75rem;
		filter: invert(26%) sepia(66%) saturate(3908%) hue-rotate(328deg) brightness(97%) contrast(89%);
	}

	&__location{
		font-size: 1.2rem;
		display: flex;
		
	}

	&__rating{
		background-color: var(--color-primary);
		color: #fff;
		margin-left: 3rem;
		padding: 0 2.25rem;
		align-self: stretch;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	&__rating-average{
			font-size: 2.25rem;
			font-weight: 300;
			margin-bottom: -3px;
	}

	&__rating-count{
		font-size: .8rem;
		text-transform: uppercase;
	}
}

.btn-inline{
	border:none;
	color:var(--color-primary);
	font-size: inherit;
	border-bottom: 1px solid currentColor;
	padding-bottom: 2px;
	display: inline-block;
	background-color: transparent;
	cursor: pointer;

	&:hover{
		color:var(--color-grey-dark-1);
	}

	&:focus{
		ouline:none;
		animation: pulsate 1s infinite;
	}

}
@keyframes pulsate{
	0%{
		transform: scale(1);
		box-shadow: none;
	}

	50%{
		transform:scale(1.05);
		box-shadow:0 1rem 4rem rgba(0,0,0,.25);
	}

	100%{
		transform: scale(1);
		box-shadow: none;
	}
}

.paragraph:not(:last-of-type){
	margin-bottom: 2rem;
}

.list{
	list-style: none;
	margin-left: 3rem 0;
	padding: 3rem 0;
	border-top: var(--line);
	border-bottom: var(--line);
 	display: flex;
 	flex-wrap: wrap;

	&__item{
		flex:0 0 50%;
		margin-bottom: .7rem;
	}

	&__item::before{
		content: "";
		display: inline-block;
		height: 1rem;
		width: 1rem;
		margin-right: .7rem;

		background-color: var(--color-primary);
		mask-image:url(../img/SVG/chevron-right.svg);
		mask-size:cover;
	}
}


.recommend{
	font-size: 1.3rem;
	color: var(--color-grey-dark-3);

	display: flex;
	align-items: center;

	&__count{
		margin-right: auto;
	}

	&__friends{
		display: flex;
	}

	&__photo{
		box-sizing: content-box;
		height: 4rem;
		width: 4rem;
		border-radius:50%;
		border:3px solid #fff;

		&:not(:last-child){
			margin-right: -2rem;
		}
	}
}

.review{
	background-color: #fff;
	box-sizing: var(--shadow-light);
	padding: 3rem;
	margin-bottom: 3.5rem;
	position: relative;
	overflow: hidden;

	&__text{
		margin-bottom: 2rem;
		z-index:10;
		position: relative;

	}

	&__user{
		display: flex;
		align-items: center;
	}

	&__photo{
		height: 4.5rem;
		width: 4.5rem;
		border-radius: 50%;
		margin-right: 1.5rem;
	}

	&__user-box{
		margin-right: auto;
	}

	&__user__name{
		font-size: 1.1rem;
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: .4rem;
	}

	&__user-date{
		font-size:1rem;
		color: var(--color-grey-dark-3);
	}	

	&__rating{
		color: var(--color-primary);
		font-size: 2.2rem;
		font-weight: 300;
	}
	&::before{
		content:"\201C";
		position: absolute;
		top:-2.75rem;
		left:-1rem;
		line-height: 1;
		font-size: 20rem;
		color: var(--color-grey-light-2);
		font-family: sans-serif;
		z-index: 0;
	}	
}

.cta{
	padding: 3.5rem 0;
	text-align: center;

	&__book-now{
		font-size: 2rem;
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 2.5rem;
	}
}

.btn{
	font-size: 1.5rem;
	font-weight: 300;
	text-transform:uppercase;
	border-radius: 100px;
	border:none;
	background-image: linear-gradient(to right,  var(--color-primary-light),var(--color-primary-dark));
	color: #fff;
	cursor: pointer;
	position: relative;

	& > *{
		display: inline-block;
		height: 100%;
		width: 100%;
		transition:all .2s;
	}

	&__visible{
		padding: 2rem 7.5rem;
	}

	&__invisible{
		position: absolute;
		left:0;
		top: -100%;
		padding: 2rem 0;
	
	}

	&:hover{
		background-image: linear-gradient(to left,var(--color-primary-light),var(--color-primary-dark));
	}

	&:hover &__visible{
	transform: translateY(100%);
	}

	&:hover &__invisible{
		top: 0;
	}

	&:focus{
		outline: none;
		/*animation: pulsate 1s infinite;*/
	}
}